* {
  margin: 0;
  padding: 0;
}
.App {
  background-color: #0c192c;
  display: flex;
  height: 100vh;
  width: 100vw;
}
.menu {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 5px;
  box-sizing: border-box;
  color: white;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 5px;
}
.canvas-wrapper {
  width: 100%;
  height: 100%;
}
.mode-button {
  padding: 5px;
  border: 1px solid white;
  width: 70px;
  text-align: center;
  cursor: pointer;
}
.mode-button.active {
  background: white;
  color: black;
}
